declare global {
  interface Window {
    Telegram: {
      WebApp: any;
    };
  }
}

const tg = window.Telegram.WebApp;

export function useTelegram() {
  const onClose = () => {
    tg.close();
  };

  const onShare = () => {
    tg.switchInlineQuery('    ! ');
  };

  const onBuy = async () => {
    try {
      const result = await tg.showPopup({
        title: ' ',
        message: '  - 1 Star',
        buttons: [
          { type: 'buy', text: '', params: { amount: 1 } },
          { type: 'cancel', text: '' }
        ]
      });
      
      return result;
    } catch (error) {
      console.error('Payment error:', error);
      return false;
    }
  };

  return {
    onClose,
    onShare,
    onBuy,
    tg,
    user: tg.initDataUnsafe?.user,
  };
}
